export async function getVideoById(id: string) {
  // return get(`getVideoById/${id}`);
  return new Promise<any>((resolve, reject) => {
    if (!socket) {
        reject('No socket connection.');
    } else {
        socket.emit("getVideoById", id, (response: any) => {
            if (response.error) {
                console.error(response.error);
                reject(response.error);
            } else {
              console.log(response);
              
                resolve(response);
            }
        });
    }
});
}

export async function saveCurrentVideo(videoinfo: any) {
  return post("saveCurrentVideo", videoinfo)
}

export async function queryVideo(query: any) {
  return post("queryVideos", query)
}

export async function saveVideoList(videoList: Typing.VideoList[]) {
  // console.log("test", videoList);
  return post("saveVideoList", {
    videoList: videoList
  })
}

export function emitChanges(change: DeepPartial<Typing.VideoInfo>) {
  socket.emit("change", change)
}

export function initSocket(callback: any) {
  socket.on("updateFromServer", callback)
}

export function initTrack(callback: any) {
  socket.on('message', callback);
}

export async function download() {
  const url = `${dataServerUrl}/` + 'download';
  axios({
      url,
      method: 'GET',
      responseType: 'blob', // important
  }).then((response) => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', 'archive.zip'); //or any other extension
      document.body.appendChild(link);
      link.click();
  });
}