import { createApp } from "vue";
// import { createHead } from '@vueuse/head'

import App from "./components/App.vue";
import { store, key } from '@/store/index'
// import store from "./store";

import {
    Checkbox, Card, Tag, Tree, Table, Select, Button, Form, InputNumber, Input, Popconfirm
} from "ant-design-vue";

createApp(App)
    .use(store, key)
    .use(Checkbox).use(Card).use(Tag).use(Tree).use(Table).use(Select).use(Button).use(Form).use(InputNumber).use(Input).use(Popconfirm)
    .mount("#app");
